import Vue from 'vue'
import Router from 'vue-router'

// Containers
const DefaultContainer = () => import('@/containers/DefaultContainer')

// Views
const Dashboard = () => import('@/views/Dashboard')
const CaregiverGroups = () => import('@/views/CaregiverGroup')
const Curriculum = () => import('@/views/Curriculum')
const DeactivedCaregivers = () => import('@/views/DeactivedCaregivers')
// const Charts = () => import('@/views/Charts')
const Inbox = () => import('@/views/apps/email/Inbox')
const Sent = () => import('@/views/apps/email/Sent')
const Compose = () => import('@/views/apps/email/Compose')
const Message = () => import('@/views/apps/email/Message')
const Trash = () => import('@/views/apps/email/Trash')
const Favorite = () => import('@/views/apps/email/Favorite')
const Page404 = () => import('@/views/pages/Page404')
const Page500 = () => import('@/views/pages/Page500')
const Login = () => import('@/views/pages/Login')
const ForgetPassword = () => import('@/views/pages/ForgetPassword')
const Logout = () => import('@/views/logout')
const CaregiverRegister = () => import('@/views/pages/CaregiverRegister')
const Notifications = () => import('@/views/Notification')

Vue.use(Router)

const router = new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/pages/login/',
      name: 'Login',
      component: Login,
      children: [
        {
          path: '/pages/login/',
          name: 'Login',
          component: Login,
          children: [
            {
              name: 'Login',
              path: '/pages/login/?id='+23,
              redirect: '/pages/login/',
              component: Login,
            }            
          ]
        }
      ]     
    },
    {
      path: '/',
      redirect: '/pages/forgetPassword/',
      name: 'ForgetPassword',
      component: ForgetPassword,
      children: [
        {
          path: '/pages/forgetPassword/',
          name: 'ForgetPassword',
          component: ForgetPassword,
        }
      ]     
    },
    {
      path: '/dashboard',
      redirect: '/dashboard',
      name: 'Home',
      component: DefaultContainer,
      meta: {requiresAuth: true},
      children: [
        {
          path: '/dashboard',
          name: 'Dashboard',
          component: Dashboard,
          children: [
            {
              path: '/dashboard/allcaregivers',
              name: 'All Caregivers',
              component: Dashboard,
              meta: {requiresAuth: true}
            },
            {
              path: '/dashboard/initialtraining',
              name: 'Initial Training',
              component: Dashboard,
              meta: {requiresAuth: true}
            },
            {
              path: '/dashboard/annualduedatesoon',
              name: 'Annual Due Date Soon',
              component: Dashboard,
              meta: {requiresAuth: true}
            },
            {
              path: '/dashboard/annualduedateoverdue',
              name: 'Annual Due Date Over Due',
              component: Dashboard,
              meta: {requiresAuth: true}
            }
          ]
        }
      ]
    },    
    {
      path: '/curriculum',
      name: 'Curriculum',
      component: DefaultContainer,
      children: [
        {
          path: '/curriculum',
          name: 'Curriculum',
          component: Curriculum,
          children: [
            {
              name: 'Initial Training',
              path: '/curriculum/initialtraining',
              component: Curriculum,
            },
            {
              name: 'Annual Training',
              path: '/curriculum/annualtraining',
              component: Curriculum,
            },
          ]
        }
      ]      
    },
    {
      path: '/deactivedcaregivers',
      name: 'Deactived Caregivers',
      component: DefaultContainer,
      children: [
        {
          path: '/deactivedcaregivers',
          name: 'Deactived Caregivers',
          component: DeactivedCaregivers
        }
      ]
    },
    {
      path: '/notification',
      name: 'Notifications',
      component: DefaultContainer,
      children: [
        {
          path: '/notification',
          name: 'Notifications',
          component: Notifications
        }
      ]
    },
    {
      path: '/messagelog',
      name: 'Message Log',
      component: DefaultContainer,
      children: [
        {
          path: '/messagelog',
          name: 'Message Log',
          component: Inbox
        },
        {
          path: '/messagelog/sent',
          name: 'Sent',
          component: Sent
        },
        {
          path: '/messagelog/compose',
          name: 'Compose Email',
          component: Compose
        },
        {
          path: '/messagelog/message',
          name: 'Message',
          component: Message,
          props:true
        },
        {
          path: '/messagelog/trash',
          name: 'Trash',
          component: Trash,
          props:true
        },
        {
          path: '/messagelog/favorite',
          name: 'Favorite',
          component: Favorite,
          props:true
        }
      ]
    },
    {
      path: '/caregivergroups',
      name: 'Caregiver Groups',
      component: DefaultContainer,
      children: [
        {
          path: '/caregivergroups',
          name: 'Caregiver Groups',
          component: CaregiverGroups
        }          
      ]
    },
    {
      path: '/pages',
      redirect: '/pages/404',
      name: 'Pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '404',
          name: 'Page404',
          component: Page404
        },
        {
          path: '500',
          name: 'Page500',
          component: Page500
        },
        {
          path: 'login',
          name: 'Login',
          component: Login
        },
        {
          path: 'caregiverregister',
          name: 'Caregiver Register',
          component: CaregiverRegister
        }
      ]
    },
    {
      path: '/logout',
      name: 'Logout',
      component: Logout,
      children: [
        {
          path: '/logout',
          name: 'Logout',
          component: Logout,         
        }
      ]      
    },
  ]
})

